.favourites{
    width: 100%;
    background: #C4C4C4;
    &_content{
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }
    &_card{
        width: 268px;
        background: #FFFFFF;
        margin: 10px;
        padding: 10px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        flex-direction: column;
        font-size: 14px;
        font-family: system-ui, -apple-system, sans-serif;
        border-radius: 5px;
        box-shadow: rgba(0, 0, 0, 0.05) 0px 6px 24px 0px, rgba(0, 0, 0, 0.08) 0px 0px 0px 1px;
    }
}
